<template>
  <div>
    <h1>Road Trip Bingo</h1>
    <div 
      :class="{ game: true, win: bingo() }">
      <div 
        v-for="(row, i) in tiles" 
        :key="i"
        class="row">
        <figure 
          v-for="(tile, j) in row" 
          :key="j"
          :class="{ tile: true, active: tile.active }" 
          @click="toggle(tile)">
          <img :src="tile.src">
        </figure>
      </div>
    </div>
    <div class="status">{{ bingo() ? 'Bingo!' : 'No Bingo' }}</div>
    <div class="actions">
      <button
        type="button"
        class="action"
        @click="clear">
        Clear
      </button>
      <button
        type="button"
        class="action"
        @click="scramble">
        Scramble
      </button>
    </div>
    <div class="actions">
      <span>Icons <a href="https://www.flaticon.com/authors/freepik">designed by Freepik from Flaticon</a></span>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import options from '../icons'
import free from '../icons/four-leaf-clover.png'

export default {
  data () {
    return {
      tiles: []
    }
  },
  created () {
    if (!this.load()) {
      this.scramble()
    }
  },
  methods: {
    toggle (tile) {
      tile.active = !tile.active
      this.save()
    },
    bingo () {
      const rows = this.tiles.some((rows) => rows.every((tile) => tile.active))
      const columns = _.unzip(this.tiles).some((columns) => columns.every((tile) => tile.active))
      const downDiagonal = _.range(0, 5).every((i) => this.tiles[i][i].active)
      const upDiagonal = _.range(0, 5).every((i) => this.tiles[i][4-i].active)
      return rows || columns || downDiagonal || upDiagonal
    },
    clear () {
      this.tiles.forEach(row => row.forEach(tile => tile.active = false))
      this.save()
    },
    scramble () {
      this.tiles = []
      let chosen = _.sampleSize(options, 24)
      _.range(0, 5).forEach((i) => {
        this.tiles.push ([])
        _.range(0, 5).forEach((j) => {
          if (i !== 2 || j !== 2) {
            this.tiles[i].push({ src: chosen.pop(), active: false })
          } else {
            this.tiles[i].push({ src: free, active: true })
          }
        })
      })
      this.save()
    },
    load () {
      const saved = window.localStorage.getItem('saved')
      if (saved) {
        this.tiles = JSON.parse(saved)
        return true
      }
      return false
    },
    save () {
      window.localStorage.setItem('saved', JSON.stringify(this.tiles))
    }
  }
}
</script>

